







































































































































import { Component, Vue } from 'vue-property-decorator';

const baseUrl: string = process.env.BASE_URL;

interface Item {
  id: string;
  type: string;
  handle: string;
  collection: any;
  partner: any;
  manifests: any;
}

interface Resource {
  collection: string;
  id: string;
  label: string;
  identifier: string;
  source: string;
  partner: string;
  type: string;
}

interface Field {
  key: string;
  label: string;
  sortable: boolean;
}

interface InfoModal {
  id: string;
  label: string;
  item: Item;
  code: string;
  embedPlayer: string;
}

@Component({
  components: {},
})
export default class Home extends Vue {
  title: string = 'List of resources';

  loadingMessage: string = 'Loading resources...';

  endpoint: string = `${process.env.BASE_URL}/resources.json`;

  totalRows: number = 0;

  currentPage: number = 1;

  rows: number = 0;

  origin: string = window.origin;

  perPage: number = 15;

  isBusy: boolean = true;

  items: Array<Resource> = [];

  filter: string | null = null;

  filterOn: Array<string> = [];

  sortDirection: string = 'asc';

  sortBy: string = 'collection';

  infoModal: InfoModal = {
    id: 'info-modal',
    label: 'Item metadata',
    embedPlayer: '',
    code: '',
    item: {
      id: '',
      type: '',
      handle: '',
      collection: {},
      partner: {},
      manifests: [],
    },
  };

  fields: Array<Field> = [
    {
      key: 'id',
      label: 'Resource',
      sortable: false,
    },
    {
      key: 'type',
      label: 'Type',
      sortable: true,
    },
    {
      key: 'collection',
      label: 'Collection',
      sortable: true,
    },
    {
      key: 'partner',
      label: 'Partner',
      sortable: true,
    },
    {
      key: 'actions',
      label: 'Details',
      sortable: false,
    },
  ];

  private mounted(): void {
    this.fetchResource();
  }

  private info(item: Resource, index: number, button: any) {
    fetch(
      `${process.env.BASE_URL}/resources/${item.partner}/${item.collection}/${item.identifier}.json`,
    )
      .then((response: Response) => {
        if (response.ok) {
          return response.json();
        }
        throw new Error('Network response was not ok.');
      })
      .then((response: any) => {
        this.infoModal.embedPlayer = `${baseUrl}/#/player/${item.partner}/${item.collection}/${item.identifier}`;
        this.infoModal.code = JSON.stringify(response, null, 2);
        this.infoModal.label = response.label;
        this.infoModal.item.id = response.id;
        this.infoModal.item.type = response.type;
        this.infoModal.item.partner = response.partner;
        this.infoModal.item.collection = response.collection;
        this.infoModal.item.handle = response.handle;
        this.infoModal.item.manifests = response.manifests;
      })
      .finally(() => {
        this.$root.$emit('bv::show::modal', this.infoModal.id, button);
      })
      .catch((error) => {
        // eslint-disable-next-line
        console.log(`Error! Could not reach the API. ${error}`);
      });
  }

  private onFiltered(filteredItems: any) {
    // Trigger pagination to update the number of buttons/pages due to filtering
    this.totalRows = filteredItems.length;
    this.currentPage = 1;
  }

  private resetInfoModal() {
    this.infoModal.code = '';
    this.infoModal.item = {
      id: '',
      type: '',
      collection: {},
      partner: {},
      handle: '',
      manifests: [],
    };
  }

  private fetchResource(): void {
    this.isBusy = true;
    this.items = [];
    fetch(this.endpoint)
      .then((response: Response) => {
        if (response.ok) {
          return response.json();
        }
        throw new Error('Network response was not ok.');
      })
      .then((response: any) => {
        response.data.map((resource: Resource) => {
          resource.source = `/player/${resource.partner}/${resource.collection}/${resource.identifier}`;
          return this.items.push(resource);
        });
        this.totalRows = response.data.length;
      })
      .finally(() => {
        this.isBusy = false;
      })
      .catch((error) => {
        // eslint-disable-next-line
        console.log(`Error! Could not reach the API. ${error}`);
      });
  }
}
